import React from 'react';
import { CurrentUserContext } from '../context/CurrentUserContext';


export default function Card({ card, onCardClick, onCardLike, onCardDelete}) {
    const currentUser = React.useContext(CurrentUserContext);
    const name = card['name'];
    const link = card['link'];
    const likes = card['likes'].length;

    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, —è–≤–ª—è–µ–º—Å—è –ª–∏ –º—ã –≤–ª–∞–¥–µ–ª—å—Ü–µ–º —Ç–µ–∫—É—â–µ–π –∫–∞—Ä—Ç–æ—á–∫–∏
    const isOwn = card.owner === currentUser._id;


    // –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è
    const cardDeleteButtonClassName = (
        `card__button card__button_type-delete ${!isOwn && 'card__button_state-invisible'}`
    );

    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, –µ—Å—Ç—å –ª–∏ —É –∫–∞—Ä—Ç–æ—á–∫–∏ –ª–∞–π–∫, –ø–æ—Å—Ç–∞–≤–ª–µ–Ω–Ω—ã–π —Ç–µ–∫—É—â–∏–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º
    const isLiked = card.likes.some(i => i === currentUser._id);

    // –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ –ª–∞–π–∫–∞
    const cardLikeButtonClassName = `card__button card__button_type-like ${isLiked && 'card__button_state-selected'}`;

    function handleClick() {
        onCardClick(card);
    }

    function handleLikeClick(){
        onCardLike(card)
    }

    function handleDeleteClick(){
        onCardDelete(card)
    }

    return (
        <div className="card">
            <img draggable="false" className="card__image" src={link} alt={name} onClick={handleClick}></img>
            <h2 className="card__title">{name}</h2>
            <button className={cardLikeButtonClassName} onClick={handleLikeClick}></button>
            <p className="card__like-counter">{likes}</p>
            <button className={cardDeleteButtonClassName} onClick={handleDeleteClick}></button>
        </div>
    )
} 