// ProtectedRoute.js

import React from 'react';
import { Route, Redirect } from "react-router-dom";

// —ç—Ç–æ—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –¥—Ä—É–≥–æ–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –≤ –∫–∞—á–µ—Å—Ç–≤–µ –ø—Ä–æ–ø—Å–∞
// –æ–Ω —Ç–∞–∫–∂–µ –º–æ–∂–µ—Ç –≤–∑—è—Ç—å –Ω–µ–æ–≥—Ä–∞–Ω–∏—á–µ–Ω–Ω–æ–µ —á–∏—Å–ª–æ –ø—Ä–æ–ø—Å–æ–≤ –∏ –ø–µ—Ä–µ–¥–∞—Ç—å –∏—Ö –Ω–æ–≤–æ–º—É –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—É
const ProtectedRoute = ({ component: Component, ...props  }) => {
  return (
    <Route exact path={props.path}>
      {
        () => props.loggedIn ? <Component {...props} /> : <Redirect to={props.redirectTo} />
      }
    </Route>
)}

export default ProtectedRoute; 